import React, { Component, Fragment } from 'react';

import { Card, Row, Table, Popconfirm, Col } from 'antd';

import Page from 'components/LayoutComponents/Page';    
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';

import { factorOfTrainingScore } from 'dump/data';

class FactorOfTraningScore extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.title = 'Tham số quy chế'
    this.urlOfBtnEdit = '#/factorOfTrainingScore/form/edit/';
    this.columns = [
      {
        title: 'Tên quy chế',
        dataIndex: 'regulation',
        key: 'regulation',
      },
      {
        title: 'Mã tham số',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Tên tham số',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Giá trị',
        dataIndex: 'value',
        key: 'value',
      },
      {
        title: 'Operation',
        dataIndex: 'operation',
        render: (text, record) =>
          factorOfTrainingScore.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`${this.urlOfBtnEdit}${record.code}`} />
              <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
  }

  render() {
    return (
      <Card title={this.title}>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={factorOfTrainingScore} columns={this.columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FactorOfTraningScore />
  </Page>
);



